import { useState } from 'react';
import Image from 'next/image';
import { Send } from "lucide-react"; 
import { FaInstagram, FaFacebookF } from "react-icons/fa";


export default function CommentsSection() {
  const [comments, setComments] = useState([
    {
      name: 'Matthew Larson',
      date: '8 December 2025',
      text: 'This is super helpful! I’ve always found GST and superannuation a bit confusing, but your breakdown makes it much clearer. Excited to read more about lodging taxes online!',
      avatar: '/author1.png',
    },
    {
      name: 'Murtaza Ali Haider',
      date: '9 December 2025',
      text: 'Thank you so much! I’m glad you found the breakdown helpful. I’ll definitely be sharing more insights on lodging taxes online soon — stay tuned!',
      avatar: '/author2.png',
    },
  ]);

  

  const [newComment, setNewComment] = useState('');

  const handleAddComment = () => {
    if (newComment.trim() !== '') {
      const newEntry = {
        name: 'You',
        date: new Date().toLocaleDateString(),
        text: newComment,
        avatar: '/your-avatar.jpg',
      };
      setComments([...comments, newEntry]);
      setNewComment('');
    }
  };

  const handleDeleteComment = (index) => {
    const updatedComments = comments.filter((_, i) => i !== index);
    setComments(updatedComments);
  };

  return (
    <div className=" mx-auto p-4 bg-white shadow-md rounded-md mt-8">
        
      <h2 className="text-xl font-semibold mb-4">Add Comments</h2>
      <div className='flex justify-between'>
      <div>
      <div className="flex gap-2 mb-4">
        <input
          type="text"
          placeholder="Add a comment...."
          value={newComment}
          onChange={(e) => setNewComment(e.target.value)}
          className="flex-1 p-2  "
        />
        <button
          onClick={handleAddComment}
          className=" text-black text-[30px] rounded-lg ml-10 hover:bg-gray-800 transition"
        >
          ➤
        </button>
      </div>
      </div>

<div>
      
    
        <div className="mt-3 flex justify-start items-center gap-3 mb-4 text-black">
        <span className="font-semibold">Share:</span>
        <div className=' bg-orange-100 rounded-full p-2'>
                   <Send className="w-[20px] h-[20px] cursor-pointer   hover:text-orange-500 transition" />
                   </div>
                   <div className=' bg-orange-100 rounded-full p-2'>
                   <FaInstagram className="w-[20px] h-[20px] cursor-pointer  bg-orange-100 hover:text-orange-500 transition" />
                   </div>
                   <div className=' bg-orange-100 rounded-full p-2'>
                   <FaFacebookF className="w-[20px] h-[20px] cursor-pointer  bg-orange-100 hover:text-orange-500 transition" />
                   </div>
                 </div>
                 
      
      </div>
      </div>

      {comments.map((comment, index) => (
        <div
          key={index}
          className={`p-4 rounded-md shadow-md mb-4 ${index % 2 === 0 ? 'bg-orange-100' : 'bg-gray-100'}`}
        >
            <div className='flex justify-between'>
          <div className="flex items-center gap-2 mb-2 ">
            <Image src={comment.avatar} alt={comment.name} width={40} height={40} className="rounded-full" />
            <div>
              <p className="font-bold">{comment.name}</p>
              <p className="text-sm text-gray-500">{comment.date}</p>
            </div>
           
          </div>
          <div>
          <button className="text-sm text-gray-600 hover:text-gray-800">↪ Reply</button>
          </div>
          </div>
          
          <p className="text-gray-700 mb-2">{comment.text}</p>
          <div className="flex justify-between items-center">
           
            <button
              onClick={() => handleDeleteComment(index)}
              className="text-sm text-red-600 hover:text-red-800"
            >
              Delete
            </button>
          </div>
        </div>
      ))}

        
            
      
    </div>
  );
}
